<template>
  <div id="notebook-list">
    <header>
      <a href="#" class="btn" @click.prevent="onCreate"><i class="iconfont icon-plus"></i>新建笔记本</a>
    </header>
    <main>
      <div class="layout">
        <h3>笔记本列表（{{notebooks.length}}）</h3>
        <div class="book-list">
          <router-link class="notebook" v-for="notebook in notebooks" :to="`/note?notebookId=${notebook.id}`">
            <div>
              <span class="iconfont icon-notebook"></span>{{notebook.title}}
              <span>{{notebook.noteCounts}}</span><span class="action" @click.stop.prevent="onEdit(notebook)">编辑</span>
              <span class="action" @click.stop.prevent="onDelete(notebook)">删除</span>
              <span class="date">{{ notebook.friendlyCreatedAt }}</span>
            </div>
          </router-link>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import Auth from '@/api/auth'
import Router from '@/router/index'
import Notebooks from '@/api/notebooks'
import {friendlyDate} from '@/helpers/util'


export default {
  name: "NotebookList",
  data(){
    return {
      notebooks: [],
    }
  },
  /*created() {
    // 如果没有登录，点击笔记本，自动跳转到登录页面
    Auth.getInfo().then(res => {
      console.log("res:",res)
      if(!res.isLogin){
        Router.push({ path: '/login'})
      }
    })

    Notebooks.getAll().then(res => {
      this.notebooks = res.data
      console.log('notebook:',res)
    })
  },*/
  async created() {
    // 如果没有登录，点击笔记本，自动跳转到登录页面
    const res = await Auth.getInfo()
    if(!res.isLogin){
      Router.push({ path: '/login'})
      return
    }
    const res2 = await Notebooks.getAll()
    this.notebooks = res2.data
  },

  methods: {
    onCreate() {
      this.$prompt('输入笔记本名称', '创建笔记本', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^.{1,30}$/,
        inputErrorMessage: '标题不能为空，且不超过30个字符'
      }).then(({ value }) => {
        return Notebooks.addNotebook({ title: value })
      }).then(res => {
        // 新增的时候，带上时间
        res.data.friendlyCreatedAt = friendlyDate(res.data.createdAt)
        this.notebooks.unshift(res.data)
        this.$message.success(res.msg)
        /*this.$message({
          type: 'success',
          message: res.msg
        })*/
      })
    },


    onCreate1(){
      let title = window.prompt('创建笔记本')
      if(title.trim() === ''){
        alert('笔记本名不为空')
        return
      }
      Notebooks.addNotebook({title})
        .then(res => {
          alert(res.msg)

          // 新增的时候，带上时间
          res.data.friendlyCreatedAt = friendlyDate(res.data.createdAt)
          this.notebooks.unshift(res.data)
        })
    },

    onEdit(notebook){
      let title = ''
      this.$prompt('输入笔记本名称', '修改笔记本', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^.{1,30}$/,
        inputValue: notebook.title,
        inputErrorMessage: '标题不能为空，且不超过30个字符'
      }).then(({ value }) => {
        title = value
        return Notebooks.updateNotebook(notebook.id, { title })
      }).then(res => {
        notebook.title = title
        this.$message.success(res.msg)
      })



      /*let title = window.prompt('修改标题', notebook.title)
      Notebooks.updateNotebook(notebook.id, {title})
        .then(res => {
          alert(res.msg)
        })
      // 修改页面上的 title
      notebook.title = title*/
    },
    onDelete(notebook){
      this.$confirm('此操作将永久删除该笔记本, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        return Notebooks.deleteNotebook(notebook.id)
      }).then(()=>{
        this.notebooks.splice(this.notebooks.indexOf(notebook),1)
        this.$message.success(res.msg)
      })


      /*let isConfirm = window.confirm('你确定要删除吗？')
      if(isConfirm){
        Notebooks.deleteNotebook(notebook.id)
          .then(res => {
            console.log(res)
            alert(res.msg)
            // 在页面上 删除 这一个笔记本
            this.notebooks.splice(this.notebooks.indexOf(notebook),1)
          })
      }*/
    }
  }
}
</script>

<style lang="less" scoped>
@import "../assets/css/notebook-list.less";
</style>



