<template>
  <div id="note" class="detail">
    <NoteSideBar></NoteSideBar>
    <!--    <h1>notebookId : {{$route.query.notebookId}}</h1>
        <h1>noteId : {{$route.query.noteId}}</h1>-->

    <div class="note-detail">
      <div class="note-bar">
        <span>创建日期：{{ currentNote.createdAtFriendly }}</span>
        <span>更新日期：{{ currentNote.updatedAtFriendly }}</span>
        <span>{{ currentNote.statusText }}</span>
        <span class="iconfont icon-delete"></span>
        <span class="iconfont icon-fullscreen"></span>
      </div>
      <div class="note-title">
        <input type="text" :value="currentNote.title" placeholder="输入标题">
      </div>
      <div class="editor">
        <textarea v-show="true" :value="currentNote.content" placeholder="输入内容, 支持 markdown"></textarea>
        <div class="preview markdown-body" v-html="" v-show="false"></div>
      </div>
    </div>
  </div>
</template>

<script>
import Auth from '@/api/auth'
import NoteSideBar from '@/components/NoteSideBar'

export default {
  name: "NoteDetail",
  components: {
    NoteSideBar,
  },
  data() {
    return {
      msg: '笔记详情页',
      currentNote: {
        title: '我的笔记',
        content: '我的笔记内容',
        createdAtFriendly: '',
        updatedAtFriendly: '',
        statusText: ''
      }
    }
  },
  created() {
    // 如果没有登录，点击笔记详情，自动跳转到登录页面
    Auth.getInfo().then(res => {
      if (!res.isLogin) {
        this.$router.push({path: '/login'})
      }
    })
  }
}
</script>

<style lang="less" scoped>
@import "../assets/css/note-detail.less";

#note {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;
}
</style>



