<template>
  <div id="trash-detail">
    <h1>{{msg}} : {{$route.params.noteId}}</h1>
    <h1>notebookId : {{$route.query.notebookId}}</h1>
    <h1>noteId : {{$route.query.noteId}}</h1>
  </div>
</template>

<script>
import Auth from '@/api/auth'

export default {
  name: "TrashDetail",
  data(){
    return{
      msg: '回收站笔记详情页'
    }
  },
  created() {
    // 如果没有登录，点击回收站，自动跳转到登录页面
    Auth.getInfo().then(res => {
      if(!res.isLogin){
        this.$router.push({ path: '/login'})
      }
    })
  }
}
</script>

<style scoped>
h1{
  color: #999;
}
</style>



