<template>
  <div id="avatar">
    <span :title="username">{{ slug }}</span>
  </div>
</template>

<script>
import Auth from '@/api/auth'
import Bus from '@/helpers/bus'


export default {
  name: "Avatar",
  data() {
    return {
      username: '未登录',
    }
  },
  created() {
    Bus.$on('userInfo', user=>{
      this.username = user.username
    })

    Auth.getInfo()
      .then(res => {
        console.log(res)
        if(res.isLogin === true){
          this.username = res.data.username
        }
      })
  },
  computed: {
    slug(){
      return this.username.charAt(0)
    }
  },
}
</script>

<style scoped>
#avatar {

}

span {
  display: inline-block;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 32px;
  border-radius: 100%;
  background: #f2b81c;
  color: #fff;
  text-shadow: 1px 0 1px #795c19;
  font-weight: bold;
  font-size: 18px;
  text-transform: uppercase;
  margin-top: 15px;
}
</style>



